<template>
  <div class="my-5">
    <v-card>
      <v-card-title>Endereos</v-card-title>
      <div class="my-2 mx-3" v-if="data.length > 0">
        <table>
          <thead>
            <tr>
              <th
                v-for="header in columnsHeader"
                :key="header"
                :class="{
                  'right-align': isRight(rightHeader, header),
                }"
              >
                {{ header }}
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="row in data" :key="row">
              <td
                v-for="(cell, index) in row"
                :key="index"
                :class="{
                  'right-align': isRight(rightHeader, index.toString()),
                }"
              >
                {{ cell }}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="my-2 mx-3" v-else>Sem endereos cadastrados!</div>
    </v-card>
  </div>
</template>

<script setup lang="ts">
const rightHeader = ["CEP", "zip_code"];
const columnsHeader = ["Endereo", "Bairro", "Cidade/UF", "CEP"];
const props = defineProps<{
  data: Array<object>;
}>();

function isRight(columns: string[], value: string) {
  return columns.includes(value);
}
</script>
