export function decimal(v: string) {
  const test = /^\d*(\.\d{0,2})?$/.test(v);
  return test || "Numero invlido. Formato esperado: 1,23";
}

export function required(v: any) {
  return (typeof v == "number" && v == 0) || !!v || "Campo obrigatrio";
}

export function requiredMultiple(v: any) {
  return v.length > 0 || "Campo obrigatrio";
}

export function validateEmail(v: string) {
  const test = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v);
  return test || "Email invlido. Formato esperado: seu@email.com";
}

export function validatePassword(v: string) {
  if (!v) return true;
  const re = /^(?=.*\d)(?=.*[!@#$%^&*.])(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
  return (
    re.test(v) ||
    "A Senha deve conter 8 ou mais caracteres, com pelo menos uma letra maiscula, uma minscula, um caractere especial (!@#$%^&*.) e um nmero. Ex: Abcd531@"
  );
}

export function minValue(min: number) {
  return (v: number) => v >= min || `Deve ser maior ou igual a ${min}`;
}
